<template>
  <div id="list">
    <div class="page" v-for="item in items" :key="item.id">
        <div style="margin-top:20px">{{item.name}}</div>
    </div>
    <div class="btn">
       <Button :isShow="isShow"/>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import Button from './Button'
export default {
  name: 'app',
  data() {
    return {
        items: [],
        isShow: false
    }
  },
  components: {
    Button
  },
  mounted() {
    let _this = this
      axios.get('https://jsonplaceholder.typicode.com/users').then(function(res) {
        _this.items = res.data
        _this.isShow = true
      })
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
